import { defineComponent } from 'vue';
import { useStore } from 'vuex';
export default defineComponent({
  name: 'tableuser',
  props: {
    title: {
      type: String,
      defulat: ''
    },
    listData: {
      type: Array,
      required: true
    },
    propList: {
      type: Array,
      required: true
    },
    showIndexColumn: {
      type: Boolean,
      defulat: false
    },
    showSelectColumn: {
      type: Boolean,
      defulat: false
    },
    listCount: {
      type: Number,
      defulat: 0
    },
    page: {
      type: Object,
      defulat: () => ({
        pageCount: 0,
        pageSize: 10
      })
    },
    childrenProps: {
      type: Object,
      defulat: () => {}
    },
    showFooter: {
      type: Boolean,
      defulat: true
    }
  },
  emits: ['selectionCH', 'update:page'],
  setup(props, {
    emit
  }) {
    const store = useStore();
    const selectionchange = value => {
      emit('selectionCH', value);
    };
    const handleSizeChange = pageSize => {
      emit('update:page', {
        ...props.page,
        pageSize
      });
    };
    const handleCurrentChange = pageCount => {
      emit('update:page', {
        ...props.page,
        pageCount
      });
    };
    return {
      selectionchange,
      handleSizeChange,
      handleCurrentChange
    };
  }
});